


























































































export default{
	data:{
		order:[],//订单数据
		checked : false,
		checkedAll : false,
		allNames: [],//购物车内所有商品id，用来对比是否全选中
		checkedNames: [],//已经选中的商品id，==allNames时，全选
		total_price: "0.00"//选中商品总价
	},
	components:{
		
	},
	methods:{
		//获取所有购物车内商品信息
		getCartInfo: function(){
			var _this = this;
			wx.request({
				url: 'https://www.slluks.com/index.php/app/goods/goodslist',
				data: {
					'page' : 3,
				},
				header: {
					'content-type': 'application/json' // 默认值
				},
				success: function(res) {
					_this.order = [];
						res.data.forEach(
							item => {
								item.checked = false;
								_this.allNames.push(item.goods_id);
								_this.order.push(item);
							}
						);
					console.log(_this.checkedNames);
					uni.showToast({
						title: '加载成功',
						duration: 2000,
						icon: 'none'
					});
				},
				fail: function(){
					uni.showToast({
						title: '加载失败',
						duration: 2000,
						icon: 'none'
					});
				}
			})
		},
		//商品选择状态改变时调用
		checkboxChange:function(e){
			//判断是否全选
			if(e.detail.value.length == this.order.length){
				this.checkedAll = true;
			}else if(e.detail.value.length < this.order.length){
				this.checkedAll = false;
			}
			this.checkedNames = e.detail.value;
			console.log(e.detail.value)
		},
		//全选选项改变时调用
		checkAllChange:function(e){
			if(e.detail.value[0] == "ok"){
				for(var i = 0 ; i < this.order.length ; i++){
					this.order[i].checked = true;
				}
				this.checkedNames = this.allNames;
				this.checkedAll = true;
			}else if(e.detail.value[0] == undefined){
				for(var i = 0 ; i < this.order.length ; i++){
					this.order[i].checked = false;
				}
				this.checkedNames = [];
				this.checkedAll = false;
			}
		}
	},
	onLoad() {
		this.getCartInfo();
	},
	onPullDownRefresh() {
		
	},
	onReachBottom() {
		
	},
	onTabItemTap(){
		this.getCartInfo();
		uni.pageScrollTo({
			scrollTop: 0,
			duration: 300
		});
	}
}



